'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Menu, X } from 'lucide-react';

const NAV_LINKS = [
  { label: 'Our Portfolios',  href: '/portfolios' },
  { label: 'Our Performance', href: '/performance' },
  { label: 'News',            href: '/news' },
  { label: 'About Us',        href: '/about' },
];

export default function Navbar() {
  const pathname    = usePathname();
  const [open, setOpen] = useState(false);

  return (
    <>
      <nav style={{
        position: 'fixed', top: 0, left: 0, right: 0, zIndex: 50,
        height: '64px',
        background: 'rgba(9,9,11,0.90)',
        backdropFilter: 'blur(16px)',
        WebkitBackdropFilter: 'blur(16px)',
        borderBottom: '1px solid var(--border)',
      }}>
        <div className="container" style={{ height: '100%', display: 'flex', alignItems: 'center' }}>

          {/* Wordmark */}
          <Link href="/" style={{ display: 'flex', alignItems: 'baseline', gap: '3px', textDecoration: 'none', flexShrink: 0 }}>
            <span style={{ fontSize: '22px', fontWeight: 800, color: 'var(--text-primary)', letterSpacing: '-0.03em' }}>
              SF Capital
            </span>
            <span style={{ width: '5px', height: '5px', borderRadius: '50%', background: 'var(--accent)', display: 'inline-block', marginBottom: '3px', marginLeft: '1px', flexShrink: 0 }} />
          </Link>

          <div style={{ flex: 1 }} />

          {/* Desktop nav */}
          <div className="hide-mobile" style={{ display: 'flex', alignItems: 'center', gap: 'var(--space-1)' }}>
            {NAV_LINKS.map(link => {
              const active = pathname === link.href || (link.href !== '/' && pathname.startsWith(link.href));
              return (
                <Link key={link.href} href={link.href} style={{
                  fontSize: 'var(--text-sm)',
                  fontWeight: active ? 600 : 400,
                  color: active ? 'var(--text-primary)' : 'var(--text-secondary)',
                  padding: '6px 14px',
                  borderRadius: 'var(--radius-md)',
                  background: active ? 'rgba(255,255,255,0.05)' : 'transparent',
                  transition: 'color var(--transition), background var(--transition)',
                  textDecoration: 'none',
                  whiteSpace: 'nowrap',
                }}
                onMouseEnter={e => { if (!active) (e.currentTarget as HTMLAnchorElement).style.color = 'var(--text-primary)'; }}
                onMouseLeave={e => { if (!active) (e.currentTarget as HTMLAnchorElement).style.color = 'var(--text-secondary)'; }}
                >
                  {link.label}
                </Link>
              );
            })}
          </div>

          {/* Mobile toggle */}
          <button
            className="show-mobile"
            onClick={() => setOpen(o => !o)}
            aria-label="Toggle menu"
            style={{ background: 'none', border: 'none', color: 'var(--text-primary)', cursor: 'pointer', padding: 'var(--space-2)', marginLeft: 'var(--space-3)' }}
          >
            {open ? <X size={20} /> : <Menu size={20} />}
          </button>
        </div>
      </nav>

      {/* Mobile drawer */}
      {open && (
        <div style={{
          position: 'fixed', top: '64px', left: 0, right: 0, zIndex: 49,
          background: 'var(--bg-subtle)',
          borderBottom: '1px solid var(--border)',
          padding: 'var(--space-3) var(--space-4)',
          display: 'flex', flexDirection: 'column', gap: '2px',
        }}>
          {NAV_LINKS.map(link => {
            const active = pathname === link.href;
            return (
              <Link key={link.href} href={link.href} onClick={() => setOpen(false)} style={{
                padding: '11px 14px',
                borderRadius: 'var(--radius-md)',
                fontSize: 'var(--text-base)',
                fontWeight: active ? 600 : 400,
                color: active ? 'var(--text-primary)' : 'var(--text-secondary)',
                background: active ? 'rgba(255,255,255,0.05)' : 'transparent',
                borderLeft: active ? '2px solid var(--accent)' : '2px solid transparent',
                textDecoration: 'none',
              }}>
                {link.label}
              </Link>
            );
          })}
        </div>
      )}
    </>
  );
}
